.main {
  width: 100%;
  flex-grow: 1;
  background-color: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.main__background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url(../../images/main-banner.jpg);
  background-position: center;
  background-size: cover;
  opacity: 0.6;
}

.main__content {
  width: 100%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  z-index: 2;
  gap: 30px;
  padding-bottom: 8vh;
  box-sizing: border-box;
}

.main__title {
  padding-top: 46vh;
  color: #fff;
  opacity: 0.75;
  font-size: 86px;
}

.main__navigation {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: max-content;
  gap: 15px;
}

.main__navigation-link {
  text-align: center;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  opacity: 0.75;
  line-height: 1;
  padding: 10px 100px;
  border: 2px solid #fff;
  border-radius: 26px;
  text-decoration: none;
  box-shadow: 0 0 0 rgba(255, 255, 255, 1);
  transition: opacity 0.5s, box-shadow 0.4s;

}

.main__navigation-link:hover {
  opacity: 1;
  box-shadow: 0 0 14px rgba(255, 255, 255, 1) inset;
}

@media (max-width: 1050px) {
  .main__title {
    padding: 46vh 3vh 0;
  }

  .main__navigation {
    gap: 30px;
  }

  .main__navigation-link {
    font-size: 24px;
  }
}

@media (max-width: 820px) {
  .main__content {
    gap: 70px;
  }
}

@media (max-width: 670px) {
  .main__title {
    font-size: 74px;
  }
}

@media (max-width: 560px) {
  .main__title {
    padding: 40vh 2vh 0;
    font-size: 65px;
  }
}

@media (max-width: 472px) {
  .main__content {
    gap: 90px;
    justify-content: space-between;
  }

  .main__navigation {
    gap: 40px;
  }

  .main__title {
    padding: 30vh 2vh 0;
    font-size: 52px;
  }

  .main__navigation-link {
    font-size: 20px;
    border-radius: 26px;
  }
}